     <img class="m-top-logo" src="./assets/img/logo.png">
     <p class="m-top-title">恭喜您获得</p>
     <div class="m-top-money">
       <p class="money">{{money}}</p>
       <p class="unit">
         <span class="ico">最高</span>
         <span class="units">元</span>
       </p>
     </div>
     <p class="m-top-tips">可提现至微信、支付宝</p>
     <p class="m-top-btn" @click="receive">立即领取</p>
     <p class="m-top-tit">点击领取前往APP提现</p>
  </div>
  <div class="m-list">
     <p class="m-title">大家都在领</p>
     <ul class="m-list-box">
       <li v-for="(item,index) in userlist" :key="index">
         <p class="tu"><img :src="item.face"></p>
         <p class="info">
           <b>{{item.nickname}}</b>
           <span>成功领取了<em>{{item.money}}</em>元</span>
         </p>
         <p class="time">{{item.time_str}}</p>
       </li>
     </ul>
     <p class="m-more">更多请前往APP查看</p>
  </div>
  <div v-show="maskShow" class="m-mask" @click="maskEvent">
     <div class="m-mask-boxs">
       <p class="m-mask-img"><img src="./assets/img/jt.png"></p>
       <div class="m-mask-box">
         <p>1、点击右上角“<b>···</b>”</p>
         <p>2、在浏览器中打开</p>
       </div>
     </div>
  </div>
</div>
</template>

<script>
import qs from 'qs'

export default {
  name: 'app',
  components: {},
  data () {
    return {
      money: '30.66',
      userlist: [],
      maskShow: false  
    }
  },
  methods: {
     receive(){
       this.maskShow = true;
     },
     maskEvent(){
       this.maskShow = false;
     }
  },
  created: function () {
    this.$axios.post('http://zlb.vaatn.com/api/v1.user/getReceiveList').then(res => {
       if(res.status == 200){
          this.money = res.data.data.money;
          this.userlist = res.data.data.list;
       }                     
    })
  }
}
</script>

